.rightbar{
flex: 3.5;
}
.birthdaycontainer{
display: flex;
align-items: center;
}
.rightbarwrapper{
padding: 20px 20px 0 0;
}
.birthdayimg{
width: 40px;
margin-right: 10px;
}
.birthdaytext{
font-weight: 300;
font-size: 15px;
}
.adimg{
width: 100%;
border-radius: 10px;
margin: 20px 0;
}
.rightbartitle{
margin-bottom: 20px;
}
.rightbarfriendlist{
padding: 0;
margin: 0;
list-style: none;
}
.rightbarfriend{
display: flex;
align-items: center;
margin-bottom: 15px;
}
.rightbarimgcontainer{
margin-right: 10px;
position: relative;
}
.rightbarimg{
width: 40px;
height: 40px;
border-radius: 50%;
object-fit: cover;
position: relative;
z-index: -1;
}
.rightbaronline{
width: 12px;
height: 12px;
border-radius: 50%;
background-color: limegreen;
position: absolute;
z-index: -1;
top: -2px;
right: 0;
border: 2px solid white;
}
.rightbarusername{
font-weight: 500;
}
.rightbartitle{
font-size: 18px;
font-weight: 500;
margin-bottom: 10px;
}
.rightbarinfo{
margin-bottom: 30px;
}
.rightbarinfoitem{
margin-bottom: 10px;
}
.rightbarinfokey{
font-weight: 500;
margin-right: 15px;
color: #555;
}
.rightbarinfovalue{
font-weight: 300;
}
.rightbarfollowings{
display: flex;
flex-wrap: wrap;
justify-content: space-between;
}
.rightbarfollowing{
display: flex;
flex-direction: column;
margin-bottom: 20px;
cursor: pointer;
}
.rightbarfollowing span{
text-align: center;
}
.rightbarfollowingimg{
width: 120px;
height: 120px;
object-fit: cover;
border-radius: 5px;
}
