.share{
width: 100%;
height: 170px;
border-radius: 10px;
box-shadow: 0 0 16px -8px rgba(0, 0, 0, 0.68);
}
.sharewrapper{
padding: 10px;
}
.sharetop{
display: flex;
align-items: center;
}
.shareprofilepic{
width: 50px;
height: 50px;
border-radius: 50%;
object-fit: cover;
margin-right: 10px;
}
.shareinput{
border: none;
width: 80%;
outline: none;
}
.sharehr{
margin: 20px;
}
.sharebottom{
display: flex;
align-items: center;
justify-content: space-between;
}
.shareoptions{
display: flex;
margin-left: 20px;
}
.shareoption{
display: flex;
align-items: center;
margin-right: 15px;
cursor: pointer;
}
.shareoptionicon{
margin-right: 10px;
}
.shareoptiontext{
font-size: 14px;
font-weight: 500;
}
.sharebutton{
border: none;
padding: 7px;
border-radius: 5px;
background-color: green;
font-weight: 500;
margin-right: 20px;
cursor: pointer;
color: white;
}