.topbarcontainer{
height: 50px;
width: 100%;
background-color: #1877f2;
display: flex;
align-items: center;
position: sticky;
top: 0;
}
.topbarleft{
flex: 3;
}
.logo{
font-size: 24px;
margin-left: 20px;
color: white;
font-weight: bold;
cursor: pointer;
}
.searchbar{
width: 100%;
height: 30px;
background-color: white;
border-radius: 30px;
display: flex;
align-items: center;
}
.searchicon{
margin-left:10px;
font-size: 20px !important;
}
.searchinput{
border: none;
width: 70%;
outline: none;
}
.topbarcenter{
flex: 5;
}
.topbarright{
flex: 4;
display: flex;
align-items: center;
justify-content: space-around;
color: white;
}
.topbarlink{
margin-left: 10px;
font-size: 14px;
cursor: pointer;
text-decoration: none;
color: white;
}
.topbaricons{
display: flex;
}
.topbaricon{
margin-left: 15px;
cursor: pointer;
position: relative;
}
.topbariconbadge{
position: absolute;
top: -5px;
right: -5px;
background-color: red;
color: white;
border-radius: 50%;
height: 15px;
width: 15px;
text-align: center;
font-size: 12px;
}
.profileimg{
height: 32px;
width: 32px;
border-radius: 50%;
cursor: pointer;
object-fit: cover;
}