.post{
width: 100%;
border-radius: 10px;
box-shadow: 0 0 16px -8px rgba(0, 0, 0, 0.68);
margin: 30px 0;
}
.postwrapper{
padding: 10px;
}
.posttop{
display: flex;
align-items: center;
justify-content: space-between;
}
.posttopleft{
display: flex;
align-items: center;
}
.postprofileimg{
width: 32px;
height: 32px;
border-radius: 50%;
object-fit: cover;
}
.postusername{
font-size: 15px;
font-weight: 500;
margin: 0 10px;
}
.postdate{
font-size: 12px;
}
.postcenter{
margin: 20px 0;
}
.postimg{
margin: 20px 0;
width: 100%;
max-height: 500px;
object-fit: contain;
}
.postbottom{
display: flex;
align-items: center;
justify-content: space-between;
}
.postbottomleft{
display: flex;
align-items: center;
}
.likeicon{
width: 24px;
height: 24px;
cursor: pointer;
margin-right: 5px;
color: blue;
}
.hearticon{
width: 24px;
height: 24px;
cursor: pointer;
margin-right: 5px;
color: red;
}
.postlikedcounter{
font-size: 14px;
}
.postcommenttext{
cursor: pointer;
border-bottom: 1px solid gray;
font-size: 14px;
}