.login{
width: 100vw;
height: 100vh;
background-color: #f0f2f5;
display: flex;
justify-content: center;
align-items: center;
}
.loginwrapper{
width: 70%;
height: 70%;
display: flex;
}
.loginleft,.loginright{
flex: 1;
display: flex;
flex-direction: column;
justify-content: center;
}
.loginlogo{
font-size: 50px;
font-weight: bold;
color: #1775ee;
margin-bottom: 10px;
}
.logindesc{
font-size: 24px;
}
.loginbox{
height: 300px;
padding: 20px;
background-color: white;
border-radius: 10px;
display: flex;
flex-direction: column;
justify-content: space-between;
}
.logininput{
height: 50px;
border-radius: 10px;
border: 1px solid gray;
font-style: 18px;
padding-left: 20px;
outline: none;
}
.loginbutton{
height: 50px;
border-radius: 10px;
border: none;
background: #1775ee;
color: white;
font-style: 20px;
font-weight: 500;
cursor: pointer;
}
.loginforgot{
text-align: center;
color: #1775ee;
}
.loginregisterbutton{
display: flex;
justify-content: center;
align-items: center;
height: 50px;
border-radius: 10px;
border: none;
background: #42b72a;
color: white;
font-style: 20px;
font-weight: 500;
cursor: pointer;
}