.sidebar{
flex: 3;
height: calc(100vh - 50px);
overflow: scroll;
position: sticky;
top: 50px;
}
::-webkit-scrollbar{
width: 5px;
height:10px !important;
}
::-webkit-scrollbar-thumb{
background-color:rgb(179,179,179);
}
::-webkit-scrollbar-track{
background-color:#f0f2f5;
}
.sidebarwrapper{
padding: 20px;
}
.sidebarlist{
padding: 0;
margin: 0;
list-style: none;
}
.sidebarlistitem{
display: flex;
align-items: center;
margin-bottom: 20px;
}
.sidebaricon{
margin-right:15px;
}
.sidebarbutton{
width: 150px;
border: none;
padding: 10px;
border-radius: 5px;
font-weight: 500;
cursor: bold;
}
.sidebarhr{
margin: 20px 0;
}
.sidebarfriendlist{
padding: 0;
margin: 0;
list-style: none;
}
.sidebarfriend{
display: flex;
align-items: center;
margin-bottom: 15px;
margin-left: 30px;
}
.sidebarfriendimg{
height: 32px;
width: 32px;
border-radius: 50%;
object-fit: cover;
margin-right: 15px;
}
